@use "base/mixins" as m;
@use "base/variables" as v;



@media(prefers-color-scheme:dark){
    $grisOscuro: #2c2c3b; 
    body{
        background-color: $grisOscuro ;
    }


    .tagline,
    .formulario label,
    .login .acciones a,
    .olvide .acciones a{
        color: v.$blanco;
    }

    .descripcion-pagina,
    .formulario .campo input,
    .nombre-pagina{
        color: darken($color: v.$grisClaro, $amount: 13);
    }

    .formulario .campo input{
       background-color: lighten($color: $grisOscuro, $amount: 15);
       border-radius: .5rem;
       &::placeholder{
           color:darken($grisOscuro,5);
           font-weight:bold;
       }
    }

    .barra-mobile,
    .barra{
        background-color: darken($grisOscuro, $amount: 8);

        
    }

    .barra {
        
        p{
        color: v.$naranja;
        font-weight: bold;
        span{
            color:v.$grisClaro;
        }
    }
        .cerrar-sesion{
            @include m.boton(v.$naranja,v.$blanco);
            color: v.$blanco;
          
        }
    }

    .sidebar{
        @include m.tablet {
                background-color: darken($color: $grisOscuro, $amount: 5);

        }
        .sidebar-nav a{
            &.activo,
            &:hover{
                background-color: rgb(255, 136, 0);
            }
        }
    
    }





    .filtros{
        background-color: lighten($color: $grisOscuro, $amount: 10);
        h2,
        label{
            color: v.$blanco;
        }

        input[type="radio"]{
            accent-color: rgb(134, 39, 39);
        }

    }


    .dashboard{
        background-color: $grisOscuro;

        .cambiar-pass{
            color: v.$grisClaro;

            &:hover{
                color: darken($color: v.$grisClaro, $amount: 30);
            }
        }

        .formulario input:not([type=submit]){
            background-color: lighten($color: $grisOscuro, $amount: 15);
            border-radius: .5rem;
            &::placeholder{
                color:darken($grisOscuro,5);
                font-weight:bold;
            }
        }
    }

    .listado-proyectos .proyectos{
        background-color: lighten($color: $grisOscuro, $amount: 15);
        &:hover{
            background-color: lighten($color: $grisOscuro, $amount: 5);
        }
    }

    .lista-tareas .tarea{
        background-color: lighten($color: $grisOscuro, $amount: 10);
        border: none;
        p{
            color: v.$grisClaro;
        }
    }

        .modal .formulario{
        background-color: lighten($color: $grisOscuro, $amount:  3   );

        input[type="text"]{
            background-color: lighten($color: $grisOscuro, $amount: 15);
            border: none;
            color: v.$blanco;

            &::placeholder{
                color:darken($grisOscuro,5);
                font-weight:bold;
            }
        }

        legend{
            color: v.$blanco;
        }

        label{
            font-weight: bold;
        }

    }
    
}