@use 'base/variables' as v; 
@use 'base/mixins' as m; 

.dashboard{
    background-color: v.$grisClaro;

    @include m.tablet {
        display:flex;
        height: 100vh;
    }

    .principal{
        flex:1;
        overflow: auto;

    }

    .contenido{
            padding: 2rem;
            .nombre-pagina{
                font-size:2.8rem;
                text-align: center;
    }

    .formulario{

        @include m.telefono {
            li{
                background-color: red;
            }
        }


        @include m.tablet {
            input:not([type="submit"]){
                background-color: v.$blanco;
                }
            }
            input[type="submit"]{
                @include m.boton(v.$indigo,v.$blanco);
                padding: 1.5rem;
                text-transform: uppercase;
                font-weight: bold;
            }
        }
   

    }

    .cambiar-pass{
        display: block;
        color:v.$indigo;
        text-align: center;
        font-weight: bold;
        font-size: 1.7rem;
        &:hover{
            color: darken(v.$indigo, 10%);
        }

    }
}