@use 'base/variables' as v; 
@use 'base/mixins' as m; 

.olvide{
    margin-top: 5rem;
    .uptask{
        @include m.degradado (v.$rosa,v.$morado);
    }
    input[type="submit"]{
        @include m.boton(v.$rosa,v.$blanco);
    }


    .acciones{
        @include m.enlaces(v.$rosa,v.$morado);
    }
}