@use 'base/variables' as v; 
@use 'base/mixins' as m; 


.no-proyectos{
    a{
        color:v.$indigo;
    }
}

.listado-proyectos{
    list-style: none;
    padding: 0;
    margin-top: 3rem;
    @include m.grid (1,2rem);
    @include m.tablet {
        @include m.grid(2,2rem);
    }
    @include m.desktop  {
        @include m.grid(3,2rem);
    }
    .proyectos{
        background-color: lighten(v.$indigo,5);
        min-height: 10rem;
        display: flex;
        align-items: center;
        padding: 2rem;
        line-height: 1.5;
        border-radius: .4rem;
        text-align: center;
        &:hover{
            background-color: darken($color: v.$indigo, $amount: 10);
            cursor: pointer;
        }


    }
    a{
        color: v.$blanco;
        font-weight: bold;
    }
}