@use 'base/variables' as v; 
@use 'base/mixins' as m; 

.crear{
    margin-top: 5rem;
    .uptask{
        @include m.degradado (v.$cyan,v.$cyan);
    }
    input[type="submit"]{
        @include m.boton(v.$cyan,v.$blanco);
    }


    .acciones{
        @include m.enlaces(v.$cyan,v.$azul);
    }
}