@use 'base/variables' as v; 

.alerta{
    padding: 1rem 1rem 1rem 2rem;
    text-transform: uppercase;
    font-weight: bolder;
    margin-bottom: 1rem;
    font-size: 1.2rem;
    max-width: 80rem;
    text-align: center;
    position: relative;

    &.error{
        background: linear-gradient(to right, v.$rojo 0%, lighten(v.$rojo,13%) 100%);
        font-weight: bold;
        color: v.$blanco;
        border-radius: .5rem;
    }

    &.exito{
        background: linear-gradient(to right, v.$verde 0%, lighten(v.$verde,13%) 100%);
        font-weight: bold;
        color: v.$blanco;
        border-radius: .5rem;
        margin: 1rem auto;

    }

}

