@use 'base/variables' as v; 
@use 'base/mixins' as m; 

.restablecer{
    .uptask{
        @include m.degradado(v.$azul,v.$morado);
    }

    input[type="submit"]{
        @include m.boton(v.$azul,v.$blanco);


    }

    .acciones{
        @include m.enlaces(v.$azul,v.$cyan);
    }


}