@use 'base/variables' as v; 
@use 'base/mixins' as m; 

.login{
    .uptask{
        @include m.degradado(v.$morado,v.$cyan);
    }

    input[type="submit"]{
        @include m.boton(v.$morado,v.$blanco);


    }

    .acciones{
        @include m.enlaces(v.$morado,v.$cyan);
    }


}