@use 'base/variables' as v;
@use 'base/mixins' as m; 

.contenedor-nueva-tarea{
    display: flex;
    justify-content: center;
    margin-bottom: 2rem;

    .agregar-tarea{
        @include m.boton (v.$cyan,v.$blanco);
    }


}

.lista-tareas{
    list-style: none;
    padding: 0;
        margin-top: 1.5rem;
    .no-tareas{
        text-align: center;
        font-weight: bold;
        font-size: 2rem;
        background: linear-gradient(to right, v.$rojo 0%, lighten(v.$rojo,13%) 100%);
        -webkit-background-clip: text;
        color: transparent;  
     
    }

}

.tarea{
    padding: .5rem 2rem;
    border-bottom: 2px solid darken(v.$grisClaro, 5);
    margin-bottom: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &:last-of-type{
        border: none;
    }

    p{
        font-size: 1.7rem;
    }

    .opciones{

        button{
            border: none;
            font-size: 1.3rem;
            padding: .8rem 2rem;
            text-transform: uppercase;
            font-weight: bold;
            border-radius: 3px;
            margin-left: 5rem;
            cursor: pointer;
        }
        .completa{
            background-color: v.$azul;
            color: v.$blanco;
            margin-bottom: 1.5rem;
        }
        .pendiente{
            background-color: v.$naranja;
            color: v.$blanco;
            margin-bottom: 1.5rem;

        }
        .eliminar-tarea{
            background-color: v.$rojo;
            color: v.$blanco;
            margin-bottom: 1.5rem;

        }
    }



}


    .eliminar-proyecto{
        background-color: crimson;
        display: inline;
        color: #fff;
        border: none;
        font-size: 1.3rem;
        padding: 1.5rem 4rem;
        text-transform: uppercase;
        font-weight: bold;
        border-radius: 3px;
        cursor: pointer;
        width: 100%;
        @include m.tablet {
            width: auto;
            
        }
    }


